import * as React from 'react';
//import {Fragment, useState, useEffect} from 'react';
import { Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import Counter from './numberCounter';

interface JurtaSectionProps extends Partial<React.PropsWithChildren<React.ReactHTMLElement<HTMLDivElement>>> {
    green?: boolean
};

const JurtaSection: React.FC<JurtaSectionProps> = ({ children, green = true, ...props }) => {
    return <section className="text-section section section-jurta" {...props}>
        <div className="grid-top">
            <h1 className="section-title title">
                Jurta pro Zahradu HOJNOSTI
            </h1>
            <p className="text">
                <strong>Zahrada Hojnosti potebuje celoron zzem.</strong> Pro setkvn, workshopy, cvien i pro dti ze kol, kter by mohly trvit njak as mimo
                lavice.
            </p>
        </div>
        <StaticImage className='grid-img img section-image' src="../images/jurta_white.jpg" alt='Jurta - Magdalna Feilhauerov' layout='constrained' placeholder='blurred' />
        <div className="grid-bottom">
            <p className="text">
                Jurta o prmru 9 m svhledem do Zahrady HOJNOSTI poskytne pste a dky tomu
                monost pozorovat produ bez omezen, za kadho poas.
                Dky Jurt bude Zahrada Hojnosti t vkad ron dob.
                Bude slouit vem generacm, kter spojuje aktivn, tvoiv zpsob ivota.
            </p>
            <Counter green={false} />
            <p
                className={"subtitle " + (green && " green")}
                style={{ marginBottom: '1em', maxWidth: '50ch' }}
            >
                Pispt me kad. Kolik kad zns daruje, pesn tolik vlo i Nadan fond Kousek po kousku.
            </p>
            <p className="small">Dary na Jurtu je mon zaslat na speciln transparentn et:
                <strong> JURTA pro Zahradu HOJNOSTI ve Fulneku  . 301075890/0300</strong>
            </p>
            <p className="small">Nebo pes odkaz <a href="https://podpora.kousekpokousku.cz/jurta-pro-zahradu-hojnosti-ve-fulneku/">zde</a>, kde najdete darovac eky s obrazem Magdalny Feilhauerov.</p>
            <div className="button-row">
                <a href="https://podpora.kousekpokousku.cz/jurta-pro-zahradu-hojnosti-ve-fulneku/" className={"button filled " + (green && " green")}>Pispt</a>
                {green
                    ? null
                    : <Link to="/zahrada-hojnosti" className={"button  outlined " + (green && " green")}>Vce o Zahrad</Link>
                }
            </div>
        </div>
    </section>

}

export default JurtaSection;
