import * as React from 'react';
//import {Fragment, useState, useEffect} from 'react';
import Layout from './../components/layout';
import LayerWrapper from './../components/layer-wrapper';
import { StaticImage } from 'gatsby-plugin-image';
import Loadable from '@loadable/component';

// @ts-ignore


// @ts-ignore
import heroVideo from "../images/videos/zahrada_final.mp4"
// @ts-ignore
import heroVideo1366 from "../images/videos/zahrada_final_1366.mp4"
// @ts-ignore
import heroVideo768 from "../images/videos/zahrada_final_768.mp4"
// @ts-ignore
import heroVideo360 from "../images/videos/zahrada_final_360.mp4"

import { useBreakpoint } from 'gatsby-plugin-breakpoints';

// import ZahradaCarousel from '../components/zahrada-carousel'
const ZahradaCarousel = Loadable(() => import('../components/zahrada-carousel'))
const ZahradaGalerie = Loadable(() => import('../components/zahrada-galerie'))

// import AktualityCarousel from "../components/aktuality-carousel";
const AktualityCarousel = Loadable(() => import("../components/aktuality-carousel"))

import SmallMap from '../components/small-zahrada-map';
import useAktualityZahrada from '../functions/useAktualityZahrada'
import Counter from './../components/numberCounter';
import type Aktualita from '../types/aktualita'
import { Link } from 'gatsby';
// import JurtaSection from './../components/section-jurta';
interface ZahradaPageProps {


};


const ZahradaPage: React.FC<ZahradaPageProps> = ({ }): JSX.Element => {
    const breakpoints = useBreakpoint();
    const placeholderImage = <StaticImage className='img' src='../images/zahrada_hero_placeholder.png' alt='Foto Zahrady z dronu' layout='fullWidth' placeholder='blurred' />
    const videoProps = {
        className: "hero-video",
        width: "1920",
        height: "1080",
        muted: true,
        playsInline: true,
        autoPlay: true,
        loop: true,
        preload: "auto",

    }
    // React.useLayoutEffect(() => {
    //     const video = document.querySelector('.hero-video');
    //     if (video) video.addEventListener('loadeddata', () => {
    //         const target = document.getElementById('placeholderImage');
    //         // console.log(target)
    //         if (target) target.style.display = 'none';
    //     }, false)
    // }, [breakpoints])

    const aktuality: Aktualita[] = useAktualityZahrada();
    // console.log(images)




    return <Layout title="Zahrada Hojnosti | Nadan fond Kousek po Kousku" headerProps={{ color: 'green', logo: 'zahrada' }}>
        <main className="zahrada">
            <LayerWrapper className="hero">

                {/* <StaticImage className='img' src="../images/hero_placeholder.png" alt='Fotka Zahrady' layout='constrained' placeholder='blurred' />
                <h1 className="title text-center">Zahrada Hojnosti</h1> */}
                {breakpoints.noVideo
                    ? placeholderImage
                    : breakpoints.xs
                        ? <video {...videoProps}>
                            <source src={heroVideo360} type="video/mp4" />
                        </video>

                        : breakpoints.sm
                            ? <video {...videoProps}>
                                <source src={heroVideo768} type="video/mp4" />
                            </video>
                            : breakpoints.md
                                ? <video {...videoProps}>
                                    <source src={heroVideo1366} type="video/mp4" />
                                </video>
                                : breakpoints ? <video {...videoProps}>
                                    <source src={heroVideo} type="video/mp4" />
                                </video>
                                    : placeholderImage
                }
                {/* <div id="placeholderImage">
                    <StaticImage className='img' src='../images/zahrada_hero_placeholder.png' alt='Foto Zahrady z dronu' layout='fullWidth' placeholder='blurred' />
                </div> */}
                {/* <div className="overlay"></div> */}
            </LayerWrapper>


            {/* Unused claim */}
            {/* <p className="text">Jedinen projekt pro podporu sobstanosti, s jedinenm modelem hospodaen</p> */}

            <section className="section-uvod" style={{ overflow: 'auto' }}>

                <StaticImage className='img' src="../images/jahoda_right.png" alt='Ruka drzi rostlinku' layout='constrained' placeholder='blurred' />
                <Link className="link-back" to="/"><span className="text">Zpt na hlavn strnku NF <span className="highlight green">Kousek po Kousku</span></span></Link>
                {/* <section className="section section-text co-nabizi"> */}
                <p className="montserrat-subtitle " >
                    {/* <strong>Zahrada Hojnosti na pozemku Nadanho fondu Kousek po kousku je naprosto uniktn projekt,</strong> kter&nbsp;je&nbsp;pipraven na&nbsp;vstup dalch drc a&nbsp;filantrop. */}
                    Budujeme prodn zahradu - Zahradu Hojnosti. Zahradu pro <strong>tlo i dui.</strong> Pidte se? <strong>Vytvome spolen krsn, inspirativn msto pro psychickou i fyzickou relaxaci, kter bude t pro dal a dal generace.</strong>
                </p>
                <p className="montserrat-subtitle">
                    Vtme vechny, kte v n najdou svj kousek pro sebe. <strong>Kousek pohody, odpoinku, krsy, pohybu, ptelstv...</strong>
                </p>
                <p className="montserrat-subtitle">
                    Zahrada poskytne <strong>prostor pro hledn a hlub poznn sebe sama,</strong> pro tvoivou prci, setkvn, pohyb a poznvn novho prostednictvm workshop.
                </p>
                {/* <p className="text">
                        Na&nbsp;pozemku Nadanho fondu Kousek po&nbsp;kousku ve&nbsp;Fulneku, o&nbsp;rozloze 16&nbsp;000&nbsp;m<sup>2</sup>, se postupn rod vjimen <strong>msto pro tlo, mysl a&nbsp;dui, harmonizan
                            a&nbsp;inspirativn prostor pro psychickou i fyzickou relaxaci.</strong> Zzem Zahrady umon<strong> dobrovolnick projekty, sociln a mezigeneran stmelovn, edukan pesah</strong> pro&nbsp;vechny vkov skupiny i&nbsp;spoluast na&nbsp;vjimenm ekonomickm procesu.
                        Prostednictvm samosbr kvtin, bylin a&nbsp;plod jedl Zahrady pedstav toti NF naprosto <strong>uniktn zpsob hospodaen pro dosaen sobstanosti.</strong>
                    </p> */}
                {/* <h2 className="title co-nabizi">
                            Co nabz?
                        </h2> */}
                {/* </section> */}
            </section>

            <section className="section youtube-video">
                <iframe
                    width="560"
                    height="315"
                    src="https://www.youtube.com/embed/wsF5T_2csE0"
                    title="YouTube video player"
                    frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen
                />
            </section>

            <section className="text-section section section-jurta" style={{ paddingTop: 0 }}>
                <div className="grid-top">
                    <h1 className="section-title title">
                        Jurta pro Zahradu HOJNOSTI
                    </h1>
                    <p className="text">
                        <strong>Zahrada Hojnosti potebuje celoron zzem.</strong> Pro setkvn, workshopy, cvien i pro dti ze kol, kter by mohly trvit njak as mimo
                        lavice.
                    </p>
                </div>
                <StaticImage className='grid-img img section-image' src="../images/jurta_white.jpg" alt='Jurta - Magdalna Feilhauerov' layout='constrained' placeholder='blurred' />
                <div className="grid-bottom">
                    <p className="text">
                        Jurta o prmru 9 m svhledem do Zahrady HOJNOSTI poskytne pste a dky tomu
                        monost pozorovat produ bez omezen, za kadho poas.
                        Dky Jurt bude Zahrada Hojnosti t vkad ron dob.
                        Bude slouit vem generacm, kter spojuje aktivn, tvoiv zpsob ivota.
                    </p>
                </div>
            </section>
            <section className="section text-section center section-jurta-2">
                {/* <div className="counter">
                    <h2 className="green">Ji jsme vybrali</h2>
                    <p className="number"><span className="" id="number">10 000</span></p>
                    <p style={{ fontWeight: 700 }}>korun eskch</p>

                </div> */}
                <Counter />
                <p
                    className={"subtitle green"}
                    style={{ marginBottom: '0', maxWidth: '50ch', fontSize: '1.7em' }}
                >
                    <strong>Pispt me kad.</strong>
                </p>
                <p className="text"
                    style={{ marginBottom: '2em', marginTop: '0em', maxWidth: '50ch', fontSize: '1.3em' }}
                > Kolik kad zns daruje, pesn tolik vlo i Nadan fond Kousek po kousku.</p>
                <div className="button-row">
                    <a href="https://podpora.kousekpokousku.cz/jurta-pro-zahradu-hojnosti-ve-fulneku/" className={"button filled green bigger"}>Pispt</a>
                </div>
                <p className="small">Dary na Jurtu je mon zaslat tak na speciln transparentn et: <br />
                    <strong> JURTA pro Zahradu HOJNOSTI ve Fulneku  . 301075890/0300</strong>
                    <br />
                    Nebo pes odkaz <a href="https://podpora.kousekpokousku.cz/jurta-pro-zahradu-hojnosti-ve-fulneku/"><strong>zde</strong></a>,
                    kde najdete darovac eky s obrazem Magdalny Feilhauerov.
                </p>
            </section>


            <section className="prinos">
                <h2 className="title">Co Zahrada dv?</h2>
                <LayerWrapper>
                    <div style={{ height: '400px' }}></div>
                    <div>
                        <ZahradaCarousel />
                    </div>
                </LayerWrapper>
            </section>

            <section className="section section-text section-centered">
                <p className="montserrat-subtitle co-nabizi" >
                    Spoluprce s prodou je rovn. <strong>Za prci, pi a starostlivost pichz hojnost.</strong> Pokud ji vidt chceme, vnmme ji vude. U pi zrodu nabz Zahrada pdu, prostor a zzem.
                    Je povzbuzujc terapi vidt, jak se i to nejmen semnko sna uchytit, kad kvtina na louce prezentovat svoji krsu. Je to vzva a inspirace pro ns pro vechny.
                </p>
            </section>

            {/* <section className="section section-text tree-section">
                <LayerWrapper>

                </LayerWrapper>
            </section> */}

            <section className="section section-text co-nabizi">


                <ul className="flex-row">
                    <li>
                        <h3 className="title">Pda</h3>
                        <p className="text">
                            Pda poskytuje pi vemu, co po nutnch ternnch pravch zbylo, i novm semenm a rostlinm.
                            Jde o prostor vce ne 16&nbsp;000&nbsp;m<sup>2</sup>. Vznik zde kvtinov labyrint, bylinn i okrasn st.
                            Postupn se bude okrasn i jedl Zahrada roziovat, pibude jedl les.</p>
                        <p className="text">V&nbsp;Zahrad&nbsp;Hojnosti je po&nbsp;pravch stle nedostatek orn pdy. <strong>Vte&nbsp;o&nbsp;njak?</strong> Napite nm na&nbsp;<a className="mail" href="mailto:info@kousekpokousku.cz">info@kousekpokousku.cz</a></p>
                        <p className="subtitle green"><strong>Pomozte&nbsp;nm zajistit dal pdu pro&nbsp;rostliny.</strong></p>
                    </li>
                    <li>
                        <h3 className="title">Prostor</h3>
                        <p className="text">
                            Prostor slou ke&nbsp;sdlen, setkvn, tvoen jednotlivc i&nbsp;skupin. Vichni si&nbsp;mohou odzkouet sv limity pi&nbsp;fyzick prci a&nbsp;zjistit, jak dl radosti jim pin.
                            Dobrovolnictv v&nbsp;Zahrad nabude vedn vznam. Jde o&nbsp;skvl zpsob, jak&nbsp;pozitivn ovlivnit sebe a&nbsp;svt kolem ns.
                        </p>

                        <p className="text">
                            <strong>Chcete&nbsp;se&nbsp;zapojit?</strong> Navtivte&nbsp;FB&nbsp;strnku <a href="https://www.facebook.com/dobrovolniciprozahraduhojnosti.cz/">Dobrovolnci&nbsp;Zahrady&nbsp;Hojnosti</a>, kde sdlujeme aktuln informace o&nbsp;termnech a&nbsp;akcch, nebo napite na&nbsp;<a className="mail" href="mailto:info@kousekpokousku.cz">info@kousekpokousku.cz</a>.
                        </p>
                        <p className="subtitle green" ><strong>Rdi Vs pivtme</strong>.</p>
                    </li>
                    <li>
                        <h3 className="title">Zzem</h3>
                        <p className="text">
                            Zahrada poskytne zzem tak pro&nbsp;rzn druhy vdomho cvien, pro&nbsp;harmonizaci, odpoinek a&nbsp;duevn pohodu. Pro hledn sama sebe, ztien sv due, odpoutn se&nbsp;od&nbsp;kadodenn reality.
                            Je u&nbsp;na kadm, zda relaxuje u&nbsp;fyzick prce, cvien nebo si&nbsp;jen posed pod stromem. Pipravujeme i&nbsp;mnoho tematickch workshop o&nbsp;pstovn a&nbsp;zpracovn bylin, vrob mast, krm, tinktur a&nbsp;dalch zzrak.
                        </p>
                        <p className="text"><strong>Mte vlastn nmt?</strong> Pihlaste se na <a className="mail" href="mailto:info@kousekpokousku.cz">info@kousekpokousku.cz</a></p>
                        <p className="subtitle green">
                            <strong>Chcete workshop sami podat?</strong>
                        </p>
                    </li>
                </ul>

            </section>

            <section className="tree-section section">
                <LayerWrapper>
                    <StaticImage className='img tree-img' src="../images/tree_final_final.png" alt='' layout='fullWidth' placeholder='blurred' />

                    <section className="quote-section">
                        <figure className="quote-wrapper">
                            <blockquote className="komensky">"Umn, kterak vyuovat a&nbsp;uit&nbsp;se, nesm a&nbsp;neme se&nbsp;brti odjinud, ne&nbsp;od&nbsp;uitelky prody"</blockquote>
                            <figcaption className="">J.&nbsp;A.&nbsp;Komensk</figcaption>
                            {/* <blockquote className="bottom">K&nbsp;Vm bude Zahrada hojnosti k&nbsp;tomuto slouit</blockquote> */}
                        </figure>
                        <StaticImage className="img" src='../images/komensky.jpg' alt='Jan amos komensky' placeholder="blurred" layout='constrained' imgStyle={{ filter: 'brightness(1.1)' }} />
                    </section>
                </LayerWrapper>
            </section>





            <section className="financovani">
                <section className="section section-text inner">
                    <h2 className="title">
                        Hospodaen
                    </h2>

                    <p className="text">
                        <strong>Zahradu hojnosti spravuje a provozuje NF Kousek po kousku se zmrem sobstanosti.</strong>
                    </p>
                    <p className="text">
                        <strong>Jednm ze zdroj budou samosbry.</strong> Bude mon pijt na ez kvtin, sbr bylin, pozdji i bobulovin a ve, co v hojnosti Zahrada vyd.
                        Pi samosbru se nastav minimln stka a bude na kadm, zda plody prody ocen klidn i sumou vy.
                        Finann zdroje se vrt zpt do hospodaen Nadanho fondu a budou pouity na dal rozvoj Zahrady.
                    </p>
                    <p className="text">
                        <strong>Dal zdroje pinese zapojen jednotlivc, skupin a firem.</strong> Kad me zakoupit svj strom, st plotu, laviku atd., kter ponesou jejich jmenovku.
                    </p>
                    <p className="text">
                        <strong>Vme, e tento inovtorsk projekt pithne dal regionln drce a filantropy.</strong>

                    </p>
                </section>
                {/* <StaticImage className='img' src="https://image.shutterstock.com/shutterstock/photos/583423603/display_1500/stock-photo-hand-holding-credit-card-isolated-on-white-583423603.jpg" alt='' layout='constrained' placeholder='blurred'/> */}
            </section>

            <section className="section paragraph">
                {/* //todo Fix image, responsive, etc */}
                <StaticImage className='img' src="../images/paprika_hor.png" alt='Ruka drc rostlinku papriky' layout='constrained' placeholder='blurred' />
                <p className="lone-paragraph">
                    <strong>Pejeme si&nbsp;dlouhodob spolu vytvet podmnky pro spolenost, kter ije v hojnosti autentickch projev,</strong> s vdomm svho odpovdnho jednn, s umnm radovat se z kadodennho it a chut tvoit v radosti, co je<strong> nejvt dar</strong>.
                </p>
            </section>



            <section className="section section-text pravidla">
                <h2 className="title">
                    Desatero Zahrady
                </h2>
                <ol className="pravidla-list">
                    <li>Pistupuj s lskou ke vemu, co zde je a cti hojnost.</li>
                    <li>I ty jsi piloil ruku k dlu nebo se teprve tak stane. </li>
                    <li>Pijmi prci druhch i svou a dary, kter vznikly pod vedenm prody.</li>
                    <li>Vezmi pro sebe to, co neovlivn hojnost druhm.</li>
                    <li>Vyuij prostor pro relaxaci, meditaci, zklidnn.</li>
                    <li>Vyuij ist energie pro naerpn novch sil. </li>
                    <li>Zapoj se do dobrovolnickch prac a vyzkouej sv limity. </li>
                    <li>Zaij, jak je tvoit rukama, zotav tlo i mysl.</li>
                    <li>Ve co Zahrada Hojnosti nabz se vrac zpt, v podob dar potebnm. </li>
                    <li>Udruj kolobh hojnosti. Pij a nasbrej si pi samosbru kvtiny, byliny, plody, zapla za n, a se Zahrada me dle rozvjet. </li>
                </ol>
            </section>

            {/* <section className="gallery">
                <ZahradaGalerie />
            </section> */}

            <section className="aktuality-zahrada">
                <AktualityCarousel aktuality={aktuality} green />
            </section>

            <SmallMap />


        </main >
    </Layout >
}

export default ZahradaPage;



